body {
  color: #cfd4db;
  background: #272d37;
  margin: 0;
  padding: 0;
  font-family: Libre Franklin, sans-serif;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#frame {
  max-width: 800px;
  max-height: 600px;
  width: 100%;
  height: 100%;
}

#chooser {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}

.hidden {
  display: none;
}

label.section {
  color: #e91e63;
  margin-bottom: 10px;
  font-size: 110%;
}

h1 {
  color: #fff;
  margin-bottom: 20px;
  font-weight: bold;
}

#frame.error .disable, #frame.error #stage {
  display: none;
}

#frame.error {
  box-sizing: border-box;
  color: #fff;
  background-color: #300;
  border: 1px dotted #c00;
}

#frame .error {
  display: none;
}

#frame.error .error {
  display: block;
}

#frame img {
  z-index: 1;
  position: absolute;
}

#logo {
  bottom: 5px;
  right: 0;
}

::selection {
  color: #fff;
  background: #55a1db;
}

#chooser #loader, #chooser.loading form {
  display: none;
}

#chooser.loading #loader {
  display: inherit;
}

#lineup {
  bottom: 10px;
  right: 120px;
}

#stage {
  z-index: 0;
  position: absolute;
}

.disable {
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
}

#counter {
  width: 80px;
  white-space: nowrap;
  z-index: 2;
  color: #0ff;
  background: #105cb6;
  padding: 3px;
  font-family: Libre Franklin, Arial;
  font-size: 9px;
  font-weight: bold;
  position: absolute;
  top: 48px;
  left: 0;
  overflow: hidden;
}

.renderer-options label {
  font-weight: normal;
}

hr {
  border: 0;
  border-bottom: 1px solid #3d495a;
}

.btn-file {
  position: relative;
  overflow: hidden;
}

.btn-file input[type="file"] {
  min-width: 100%;
  min-height: 100%;
  text-align: right;
  filter: alpha(opacity= 0);
  opacity: 0;
  cursor: inherit;
  background: #fff;
  outline: none;
  font-size: 100px;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

